import React, {Component} from 'react';
import ReactDOM from "react-dom";
import ImageGallery from "./image-gallery";
import CssBaseline from "@material-ui/core/CssBaseline/CssBaseline";
import Paper from "@material-ui/core/Paper/Paper";
import Avatar from "@material-ui/core/Avatar/Avatar";
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import Typography from "@material-ui/core/Typography/Typography";
import FormControl from "@material-ui/core/FormControl/FormControl";
import Input from "@material-ui/core/Input/Input";
import Button from "@material-ui/core/Button/Button";
import axios from 'axios';
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import ImageGridList from "./new-image-gallery";

const styles = theme => ({
    main: {
        width: 'auto',
        display: 'block', // Fix IE 11 issue.
        marginLeft: theme.spacing.unit * 3,
        marginRight: theme.spacing.unit * 3,
        [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
            width: 400,
            marginLeft: 'auto',
            marginRight: 'auto',
        },
    },
    paper: {
        marginTop: theme.spacing.unit * 8,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
    },
    avatar: {
        margin: theme.spacing.unit,
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing.unit,
    },
    submit: {
        marginTop: theme.spacing.unit * 3,
    },
});

const endpoint = 'https://leumi-hanuka.herokuapp.com/upload';

class UploadPhoto extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isButtonDisabled: true,
            selectedFile: null,
            loaded: 0
        }
    }

    handleSelectedFile = event => {
        this.setState({
            isButtonDisabled: !event.target.files[0].type.startsWith("image"),
            selectedFile: event.target.files[0],
            loaded: 0
        });
    };

    handleUpload = () => {
        this.setState({
            isButtonDisabled: true
        });
        const data = new FormData();
        data.append('file', this.state.selectedFile, this.state.selectedFile.name);

        axios
            .post(endpoint, data, {
                onUploadProgress: ProgressEvent => {
                    this.setState({
                        loaded: (ProgressEvent.loaded / ProgressEvent.total) * 100,
                    })
                },
            })
            .then(res => {
                console.log(res.statusText);
                this.handleButtonClick();
            })
    };

    handleButtonClick(e) {
        axios.get('/images')
            .then(function (response) {
                ReactDOM.render(
                    <ImageGridList imageNames={response.data.imageNames}/>,
                    document.getElementById('root')
                );
            })
            .catch(function (error) {
                console.log(error);
            });
    };

    render() {
        const {classes} = this.props;

        return (
            <main className={classes.main}>
                <CssBaseline/>
                <Paper className={classes.paper}>
                    <Avatar className={classes.avatar}>
                        <CloudUploadIcon/>
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        העלאת תמונה
                    </Typography>
                    <form className={classes.form}>
                        <FormControl margin="normal" required fullWidth>
                            <Input type="file"
                                   accept="image/*"
                                   value={this.state.code}
                                   onChange={(event) => this.handleSelectedFile(event)}/>
                        </FormControl>
                        <Button
                            onClick={() => this.handleUpload()}
                            fullWidth
                            variant="contained"
                            color="primary"
                            className={classes.submit}
                            disabled={this.state.isButtonDisabled}
                        >
                            העלאה
                        </Button>
                        <div> {Math.round(this.state.loaded, 2)} %</div>
                        <Button
                            onClick={() => this.handleButtonClick()}
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            className={classes.submit}
                        >
                            המשך בלי העלאת תמונה
                        </Button>
                    </form>
                </Paper>
            </main>
        );
    }
}

UploadPhoto.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(UploadPhoto);